.error_container{
    background-color: #92BE3D;
    height: 100vh;
    color: #FFFFFF;
    font-family: 'Inter', sans-serif;
}
.c {
    text-align: center;
    display: block;
    position: relative;
    width: 80%;
    margin: 0 auto;
    padding-top: 150px;
}

._404 {
    font-size: 220px;
    position: relative;
    display: inline-block;
    z-index: 2;
    height: 250px;
    letter-spacing: 15px;
}

._1 {
    text-align: center;
    display: block;
    position: relative;
    letter-spacing: 12px;
    font-size: 4em;
    line-height: 80%;
}

._2 {
    text-align: center;
    display: block;
    position: relative;
    font-size: 2em;
}

.btn {
    background-color: white;
    position: relative;
    border-radius: 500px;
    display: inline-block;
    width: 358px;
    padding: 5px;
    z-index: 5;
    font-size: 25px;
    margin: 0 auto;
    color: #92BE3D;
    text-decoration: none;
    margin-top: 30px;
}

.right {
    float: right;
    width: 60%;
}

hr {
    padding: 0;
    border: none;
    border-top: 5px solid white;
    color: white;
    text-align: center;
    margin: 0px auto;
    width: 420px;
    height: 10px;
    z-index: -10;
}