.main_body{
    background-color: #F5F5FB;
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 8fr 2.5fr;
    overflow: hidden;
}
a{
    text-decoration: none;
    cursor: default;
}
.left_panel{
    padding-top: 48px;
    background-color: white;
    text-align: center;
}
.learning{
    margin-top: 48px;
    background-color: #92BE3D;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    width: 192px;
    height: 48px;
    font-family: 'Inter', sans-serif;
    color: white;
    font-weight: 600;
}
.menu .active{
    display: inline-block;
    font-weight: 600;
    font-size: 16px;
    color: #272829;
    height: 100%;
    border-right: 3px solid #92BE3D;
}
.li_active{
    border-right: 3px solid #92BE3D;
}
.menu{
    margin-top: 40px;
    text-align: left;
    display: grid;
    grid-template-columns: 20px 5fr;
    grid-column-gap: 25px;
    grid-row-gap: 40px;
    align-items: center;
    grid-template-rows: auto;
    margin-left: 72px;
}
a{
    color: #8DA1B5;
}
.right_panel{
    text-align: center;
    padding-top: 48px;
    background-color: white;
}
.main_screen{
    padding-top: 48px;
    margin-left: 40px;
    margin-right: 40px;
}
ul{
    margin-top: 40px;
}
.upgrade{
    margin-top: 40px;
}
.upgrade button{
    position: relative;
    top: -80px;
    background-color: #92BE3D;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    width: 144px;
    height: 48px;
    font-family: 'Inter', sans-serif;
    color: white;
    font-weight: 600;
}
.download_link{
    height: auto;
    width: 100%;
}
.name_style{
    font-family: Inter;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    margin: 0;
    color: #011F3B;
}
.subname_style{
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    margin: 0;
    color: #455A64;
}
.user_field{
    width: 80%;
    height: 56px;
    display: flex;
    align-items: center;
    margin:0 auto;
    text-align: left;
}
.user_img {
    height: 56px;
    width: 56px;
    border-radius: 12px;
    margin-right: 26px;
}
.userfield_list{
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.userfield_list ul{
    list-style-type: none;
    display: none;
    position: absolute;
    padding: 0;
    right: 0;
    margin-right: 30px;
    margin-top: 15px;
    background: #FFFFFF;
}
.userfield_list li:hover > ul {
    display: inherit;
    list-style-type: none;
}
.userfield_list ul li {
    min-width:90px;
	float:none;
	display:list-item;
	position: relative;
    text-align: center;
}
.userfield_list ul li a{
    color: #272829;
}
.companyefficiency{
    width: 312px;
    text-align: left;
    margin: 0 auto;
    margin-top: 64px;
}
.companyefficiency_sales {
    display: flex;
    margin: 0 auto;
    text-align: left;
    align-items: center;
}
.companyefficiency p{
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 31px;
}
.companyefficiency_property h1{
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    color: #011F3B;
    margin: 0;
}
.companyefficiency_property h2{
    margin: 0;
    margin-top: 6px;
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: #83919E;
}
.companyefficiency_property{
    margin-left: 19px;
}
.companyefficiency_prbar{
    position: absolute;
    width: 104px;
    height: 8px;
    border-radius: 21px;
    background: #DFF7B1;
    margin-right: 40px;
    right: 0;
}
.companyefficiency_production {
    display: flex;
    margin: 0 auto;
    text-align: left;
    align-items: center;
    margin-bottom: 27px;
    margin-top: 27px;
}
.companyefficiency_clientflow{
    display: flex;
    margin: 0 auto;
    text-align: left;
    align-items: center;
}
.usertasks{
    width: 312px;
    text-align: left;
    margin: 0 auto;
    margin-top: 64px;
}
.usertasks p{
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 4px;
}
.usertasks_task{
    display: flex;
    margin: 0 auto;
    text-align: left;
    align-items: center;
    margin-top: 27px;
}