table{
    width: 100%;
    margin-top: 31px;
}
.user{
    background-color: #FFFFFF;
    padding-left: 20px;
    padding-right: 20px;
    height: 50px;
    display: grid;
    grid-template-columns: 1fr 4fr 4fr 4fr 4fr;
    align-items: center;
    margin-top: 10px;
    border-radius: 15px;
}
.table_header{
    display: grid;
    grid-template-columns: 1fr 4fr 4fr 4fr 4fr;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}
td{
    color: #272829;
}
.table_header td{
    color: #828181;
}
.table_header td:hover{
    color: #92BE3D;
}